.classes-main-container .bg-text {
  text-align: center;
  position: absolute;
  line-height: 2;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}
.classes-main-container .bg-text p {
  margin-top: 3rem;
  font-weight: 400;
  font-size: 32px;
}

