/* new add from class */
#class_details {
  text-align: center;
  margin-top: 4rem;
  /* padding: 2rem 2rem 2rem 2rem;
  border: 8px solid #486255; */
  /* width: 36rem; */
  margin-left: auto;
  margin-right: auto;
  display: block;
  /* font-size: 1.7rem; */
  color: rgb(97, 94, 94);
  font-weight: 500;
  width: 80%;
}
#class_details p {
  margin-top: 1rem;
}
#class_details #title {
  margin-bottom: 2rem;
}
/* media screen */
@media screen and (max-width: 1224px) {
  .classes-main-container .bg-text {
    top: 40%;
  }
}

@media screen and (max-width: 1024px) {
  .classes-main-container .bg-text {
    top: 40%;
  }
  .classes-main-container .bg-text p {
    font-size: 31px;
  }
}
@media screen and (max-width: 920px) {
  .classes-main-container .bg-text p {
    font-size: 28px;
  }
}
@media screen and (max-width: 768px) {
  .classes-main-container .bg-image {
    width: 100%;
  }
  .classes-main-container .bg-text {
    top: 29%;
  }
  .classes-main-container .bg-text p {
    font-size: 27px;
  }
  .classes-container .whole-list-classess {
    width: 90%;
  }
}

@media screen and (max-width: 680px) {
  /* .classes-main-container .bg-text {
